<template>
  <div class="template-container">
    <div class="template-container-title">新建广告</div>

    <el-form ref="form" :model="form" :inline="true">
      <!--广告组 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">广告组</div>
        <div class="temp-item-main">
          <el-form-item label="所属广告组">
            <el-select v-model="form.adgroup_id" @change="adGroupChange" filterable remote placeholder="选择广告组" :remote-method="remoteAdgroup" :loading="loadingAdgroup">
              <el-option v-for="item in adGroupsList" :product-type="item.product_type" :key="item.adgroup_id" :label="item.adgroup_name" :value="item.adgroup_id"></el-option>
            </el-select>

          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="newAdGroup">新建广告组</el-button>
          </el-form-item>
        </div>
      </div>
      <!--广告组 end -->

      <!--创意选择 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">创意选择</div>
        <div class="temp-item-main">
          <el-form-item label="关联创意">
            <el-select v-model="form.adcreative_id" filterable placeholder="选择创意">
              <el-option v-for="item in adCreativesList" :product-type="item.product_type" :key="item.adcreative_id" :label="item.adcreative_name" :value="item.adcreative_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="newCreativity">上传创意</el-button>
          </el-form-item>
        </div>
      </div>
      <!--创意选择 end -->

      <!--广告名称 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">广告名称</div>
        <div class="temp-item-main">
          <div class="temp-item-block">
            <label class="temp-item-label" for="">曝光监控地址：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" v-model="form.impression_tracking_url" type="text" value="" />
            </div>
          </div>

          <div class="temp-item-block">
            <label class="temp-item-label" for="">点击监控地址：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" v-model="form.click_tracking_url" type="text" value="" />
            </div>
          </div>

          <div class="temp-item-block">
            <label class="temp-item-label" for="">广告名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" @input="adNameChange" v-model="form.ad_name" type="text" value="" maxlength="40" />
              <span class="temp-item-inlayremark">{{ ad_name_size }}/40</span>
            </div>
          </div>

        </div>
      </div>
      <!--广告名称 end -->

      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit">立即创建</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    if (this.account_id) {
      var params = this.$route.params || {}
      if (params.hasOwnProperty('ad_id')) {
        this.adId = params.ad_id
        this.getAd(params.ad_id)
      }
      this.getAdGroups(this.adGroupsParams) // 所属广告组
    }
    // this.getAdCommongetEnum()
    // this.getAd_Campaigns(this.adCampaignsParams) // 所属广告组
    // this.getAd_Adcreatives(this.adCreativesParams) //关联创意
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      var params = this.$route.params || {}
      if (params.hasOwnProperty('ad_id')) {
        this.adId = params.ad_id
        this.getAd(params.ad_id)
      }
      this.getAdGroups(this.adGroupsParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      submitLoading: false, // 禁止重复提交 -- loading 状态
      adId: 0,
      loadingAdgroup: false,
      adGroupsList: [],
      adCreativesList: [],
      site_setList: {},
      daily_budget: '',
      ad_name_size: 0,
      form: {
        adgroup_id: '',
        adcreative_id: '',
        ad_name: '',
        impression_tracking_url: '',
        click_tracking_url: ''
      },
      adGroupsParams: {
        page: 1,
        page_size: 100,
        adgroup_id: '',
        configured_status: '',
        adgroup_name: '',
        start_date: '',
        end_date: ''
      },
      adCreativesParams: {
        page: 1,
        page_size: 100,
        adcreative_id: '',
        adcreative_name: '',
        start_date: '',
        end_date: ''
      }
    }
  },
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    remoteAdgroup(query) {
      // if (query !== '') {
        this.loadingAdgroup = true
        setTimeout(() => {
          this.adGroupsParams.adgroup_name = query
          this.getAdGroups(this.adGroupsParams)
        }, 300)
      // } else {
      //   this.adGroupsList = []
      // }
    },
    adGroupChange(adgroup_id) {
      let obj = {}
      obj = this.adGroupsList.find(item => {
        return item.adgroup_id === adgroup_id
      })
      this.getAdcreatives({
        campaign_id: obj.campaign_id,
        product_type: obj.product_type,
        product_refs_id: obj.product_refs_id
      })
    },
    newAdGroup() {
      this.$router.push('/advertising/adGroup/new')
    },
    newCreativity() {
      this.$router.push('/advertising/adCreative/new')
    },
    adNameChange() {
      this.ad_name_size = this.form.ad_name.length
    },
    dailybudgetChange() {
      this.form.daily_budget = this.daily_budget * 10 * 10
    },
    onCancel() {
      this.$router.push('/advertising/ad')
    },
    onSubmit() {
      var params = this.form
      var that = this
      params.account_id = this.account_id
      that.submitLoading = true
      this.$axios
        .get('/Admin/Marketing_Ads/add', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/advertising/ad')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.submitLoading = false
          console.log(err)
        })
    },
    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.site_setList = respData.data['SITE_SET']
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    getAdGroups: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adgroups/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adGroupsList = respData.data.list
              console.log('adGroupsList:', that.adGroupsList)
              that.loadingAdgroup = false
              //that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
            that.loadingAdgroup = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingAdgroup = false
        })
    },
    getAdcreatives: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adcreatives/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adCreativesList = respData.data.list
              // 当前数组不存在时候才置空
              let obj = {}
              obj = that.adCreativesList.find(item => {
                return item.adcreative_id === that.form.adcreative_id
              })
              if (!obj) {
                that.form.adcreative_id = ''
              }
              console.log('adCreativesList:', that.adCreativesList)
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    getAd(ad_id) {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Ads/get', {
          params: {
            ad_id: ad_id,
            account_id: this.account_id
          }
        })
        .then(function(response) {
          var respData = response.data
          //console.log(respData)
          if (respData.code == 0) {
            if (respData.data.list[0]) {
              that.form.adgroup_id = respData.data.list[0]['adgroup_id']
              that.form.adcreative_id =
                respData.data.list[0]['adcreative']['adcreative_id']
              var campaign_id = respData.data.list[0]['campaign_id']
              //console.log(campaign_id)
              that.getAdcreatives({ campaign_id: campaign_id })
              that.form.ad_name = respData.data.list[0]['ad_name'] + '-复制'
              that.adNameChange()
              that.form.impression_tracking_url =
                respData.data.list[0]['impression_tracking_url']
              that.form.click_tracking_url =
                respData.data.list[0]['click_tracking_url']
              that.form.feeds_interaction_enabled =
                respData.data.list[0]['feeds_interaction_enabled']
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}
</style>



